@use "../variables";

.wrapper {
  // display: flex;
  // justify-content: center;
  background-color: var(--background);
  border-bottom: 2px solid #555;
}

header {
  width: clamp(300px, 100%, 1080px);
  margin: 0 auto;
  display: flex;
  gap: 8px;
  align-items: center;
  padding: 8px;
  // grid-auto-flow: column;
  // justify-content: center;
}

nav {
  padding: 8px 0;
  flex: 1;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-end;
  gap: 16px;

  a {
    text-decoration: none;
    color: var(--text-primary);
  }
}
